<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade" class='sisizng'>
      <qvSearch v-if="popShow" :name="curName" @changepop="popClick"></qvSearch>
    </transition>
    <div class="content">
      <div class="w-left">
        <ctitle :title="'区域办理量排行'"></ctitle>
        <!--<span class="c-sxr">{{curName==='drop'?'全省':curName}}</span>-->
        <div class="w-echart">
          <div ref="barEl">
            <horizontalBar :params="leftBarParams"></horizontalBar>
          </div>
        </div>
      </div>
      <div class='w-center'>
        <ctitle :title="'责任单位排名TOP10'"></ctitle>
        <blzlBtn @change="changeBar"></blzlBtn>
        <div class="w-echart" ref="barEl">
          <PaimBar :params="zrdwBarParams"></PaimBar>
        </div>
      </div>
      <div class="w-right">
        <div class="part ct-top">
          <ctitle :title="'办理方式'"></ctitle>
          <div class="inner">
            <blfsList :data="blfsListData"></blfsList>
          </div>
        </div>
        <div class="part ct-bottom">
          <ctitle :title="'信访形式'"></ctitle>
          <div class="inner">
            <!-- <circleMoveChart :data="xflyData"></circleMoveChart> -->
            <pie-chart3D class="chart" :params="xfxsPieParams"></pie-chart3D>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import timeChange from "@/components/timeChange/index";
  import ctitle from "@/components/title/index";
  import horizontalBar from "@/components/bar/HorizontalBar";
  import PaimBar from "@/components/bar/PaimBar";
  import blzlBtn from "@/components/qhpmBtn/blzlBtn";
  import qvSearch from "@/components/depSeach/qvSearch";
  import PieChart3D from "@/components/pie/PieChart3D";
  import blfsList from "@/components/list/blfsList";

  export default {
    name: "blzl",
    components: {
      ctitle,
      horizontalBar,
      PaimBar,
      timeChange,
      blzlBtn,
      qvSearch,
      PieChart3D,
      blfsList
    },
    data() {
      return {
        curName: "广东省",
        erjiName: '',
        popShow: false,
        listScroll: null,
        // 1.区域受理量动态
        shij_02: '',
        xj_02: '',
        leftBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true
          }
        },
        // 2.责任单位排名
        zrdwBarBtn: '办理量',
        zrdwBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true
          }
        },
        //3.办理方式
        blfs: '',
        bljc: '',
        blfsListData: [],
        // 4.信访来源
        xfly: '',
        bljc: '',
        xfxsPieParams: {
          data: [],
          color: ["#655CDD", "#50B3FF", "#62EDAC"],
          angle: 20,
          radius: "30%",
          labelRadius: 20,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
      };
    },
    mounted() {
      this.init(this.date, this.shej_02, this.shij_02, this.xj_02);
    },
    methods: {
      // 初始化
      init(date) {
        this.getLeftAPI(this.date, this.shej_02, this.shij_02, this.xj_02);
        this.getCenterAPI(this.date);
        this.getRightAPI(this.date, this.shej_02, this.shij_02, this.xj_02);

      },
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框确认按钮后触发
      popClick(obj) {
        this.shej_02 = !this.shej_02 ? Object.values(obj)[0] : '';
        this.shij_02 = !this.shij_02 ? Object.values(obj)[1] : '';
        this.xj_02 = !this.xj_02 ? Object.values(obj)[2] : '';
        this.getLeftAPI(this.date, this.shej_02, this.shij_02, this.xj_02);
        this.getCenterAPI(this.date);
        this.getRightAPI(this.date, this.shej_02, this.shij_02, this.xj_02);
        // this.curName = this.curName ?this.shej_02||this.shij_02||this.xj_02
        if (this.shej_02) {
          this.curName = this.shej_02
        } else if (this.shij_02) {
          this.curName = this.shij_02
        } else if (this.xj_02) {
          this.curName = this.xj_02
        }
      },
      // 1.区域办理量排行
      getLeftAPI(date, shej_02, shij_02, xj_02) {
        get_http("zrdwhxblzlqyblph", {
          shej_02: shej_02,
          shij_02: shij_02,
          xj_02: xj_02,
          bljc: this.bljc,
          day: `[${date.startDate},${date.endDate}]`,
        })
            .then(res => {
              this.leftBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
            })
            .catch(err => {
              console.log(err);
            });
      },
      // 责任单位排名TOP10按钮
      changeBar(id) {
        this.zrdwBarBtn = id;
        this.getCenterAPI(this.date);
      },
      // 2.责任单位排名TOP10柱图
      getCenterAPI(date) {
        get_http("zrdwhxblzlzrdwpm", {
          day: `[${date.startDate},${date.endDate}]`,
          Cqh:this.curName,
          gd_id3: this.zrdwBarBtn
        })
            .then(res => {
              if (res.data.data.length) {
                this.zrdwBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
              } else {
                this.zrdwBarParams.data = [];
              }
            })
            .catch(err => {
              console.log(err);
            });
      },
      getRightAPI(date, shej_02, shij_02, xj_02, id) {
        // 3.办理方式
        get_http("zrdwhxblzlblfs", {
          blfs: this.blfs,
          bljc: this.bljc,
          day: `[${date.startDate},${date.endDate}]`,
          shej_02: shej_02,
          shij_02: shij_02,
          xj_02: xj_02,
        })
            .then(res => {
              this.blfsListData = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        // 4.信访来源
        get_http("zrdwhxblzlxfly", {
          shej_02: shej_02,
          shij_02: shij_02,
          xj_02: xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
            .then(res => {
              this.xfxsPieParams.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        /*get_http("zrdwhxblzlxfly", {
          xfly:this.xfly,
          bljc:this.bljc,
          shej_02:this.shej_02,
          shij_02:this.shij_02,
          xj_02:this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.xflyData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });*/
      },
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getLeftAPI(val);
        this.getCenterAPI(val);
        this.getRightAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .w-seach
      position absolute
      top -80px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 270px
      line-height 70px
      font-size 22px
      color #cbfbff
      background url('../../../assets/search-bg2.png') no-repeat
      background-size 100% 100%
      cursor pointer

      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis

    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      box-sizing border-box

      .w-left
        display flex
        flex 1
        position relative
        font-size 0
        flex-direction column
        padding 0 .1rem 0 .2rem
        border-right 1px #91e3f7 dashed

        .w-echart
          position relative
          margin o auto
          width 100%
          height calc(100% - 0.28rem)
          overflow hidden

          > div
            height 100%

      .w-center
        display flex
        flex 2
        flex-direction column
        box-sizing border-box

        .w-echart
          display flex
          flex 1
          flex-direction row

      .w-right
        display flex
        flex 3
        flex-direction column
        box-sizing border-box

        .part
          position relative
          flex 1
          width 100%
          height 100%

          .inner
            display flex
            height calc(100% - 0.28rem)
            justify-content center

            .chart
              width 100%
              height 100%

            .timeChange
              top 0
              right 1rem


        .c-bottom
          flex 1
          display flex

          .cb-left
            flex 1

          .cb-center
            flex 1

          .cb-right
            flex 1.5

</style>
